.main-container {
  overflow: hidden;
  min-height: 100%;
  min-height: 100vh;
  padding-top: 64px;
  background-color: white;

  @include MQ($small) {
    background-color: transparent;
  }

  @include MQ($medium) {
    padding-top: 88px;
  }

}

.page-title {
  display: block;
  width: 100%;
  background: $primary-color;
  padding-bottom: 7em;

  .container {
    padding: 0 $card-padding-small;
    transition: 0.3s;

    @include MQ($small) {
      padding: 0 $card-padding-medium;
    }

    @include MQ($medium) {
      padding: 0;
    }

  }

  + .container {
    margin-top: -7em;
  }

}
